html {
	font-size: 62.5%;
}

body {
	background-color: $light-gray;
}

nav {
	color: white;
}

a { 
	text-decoration: none;
	@include transition(color 0.2s ease)
	&:visited {
		color: inherit;
	}
	&:hover {
		color: $deep-red;
	}
}

p {}

form {
	position: relative;
	width: 30%;
	padding: 5%;
	background-color: white;
}

